.iframe-container {
  width: 100%; /* Set the width of the visible area */
  height: 500px; /* Set the height of the visible area */
  overflow: hidden; /* Hide parts of the iframe that are outside this container */
  position: relative; /* Needed for absolute positioning inside */
  margin-bottom: 30px;
}

.styled-iframe {
  position: absolute;
  top: -100px; /* Adjust this value to hide the header */
  left: 0;
  width: 100%;
  height: calc(100% + 200px); /* Increase height to include hidden top and bottom */
  border: none;
}

.iframe-scale {
  width: 100%;
  height: calc(100% + 200px);
  position: relative;
  transform: scale(0.8);
  transform-origin: top left; /* You can adjust this based on where you want the scaling to pivot from */
  border: none;
  top: -50px;
  overflow: hidden;
}
