.arco-grid {
  display: grid
}

.arco-grid-rtl {
  direction: rtl
}

.arco-image-trigger {
  padding: 6px 4px;
  background: var(--color-bg-5);
  border: 1px solid var(--color-neutral-3);
  border-radius: 4px
}

.arco-image-trigger .arco-trigger-arrow {
  border: 1px solid var(--color-neutral-3);
  background-color: var(--color-bg-5)
}

.arco-image {
  position: relative;
  display: inline-block;
  border-radius: var(--border-radius-small);
  vertical-align: middle
}

.arco-image-img {
  vertical-align: middle;
  border-radius: inherit
}

.arco-image-img:focus-visible {
  box-shadow: 0 0 0 2px rgb(var(--primary-6))
}

.arco-image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%
}

.arco-image-footer {
  width: 100%;
  max-width: 100%;
  display: flex
}

.arco-image-footer-block {
  flex: auto
}

.arco-image-caption-title {
  font-size: 16px;
  font-weight: 500
}

.arco-image-caption-description {
  font-size: 14px
}

.arco-image-actions {
  padding-left: 12px
}

.arco-image-actions-list {
  display: flex;
  justify-content: flex-end;
  align-items: center
}

.arco-image-actions-item {
  font-size: 14px;
  line-height: 1;
  margin-left: 12px;
  border-radius: var(--border-radius-small);
  padding: 0;
  cursor: pointer
}

.arco-image-actions-item:first-child {
  margin-left: 0
}

.arco-image-actions-item-trigger {
  padding: 5px 4px;
  display: inline-block
}

.arco-image-with-footer-inner .arco-image-footer {
  background: linear-gradient(360deg,rgba(0,0,0,.3) 0%,rgba(0,0,0,0) 100%);
  color: var(--color-white);
  box-sizing: border-box;
  padding: 9px 16px;
  align-items: center;
  position: absolute;
  left: 0;
  bottom: 0;
  border-bottom-right-radius: inherit;
  border-bottom-left-radius: inherit
}

.arco-image-with-footer-inner .arco-image-caption-title,.arco-image-with-footer-inner .arco-image-caption-description {
  color: var(--color-white)
}

.arco-image-with-footer-inner .arco-image-actions-item:hover {
  background: rgba(0,0,0,.5)
}

.arco-image-with-footer-outer .arco-image-footer {
  color: var(--color-neutral-8);
  margin-top: 4px
}

.arco-image-with-footer-outer .arco-image-caption-title {
  color: var(--color-text-1)
}

.arco-image-with-footer-outer .arco-image-caption-description {
  color: var(--color-neutral-6)
}

.arco-image-with-footer-outer .arco-image-actions-item:hover {
  background: var(--color-neutral-2)
}

.arco-image-with-preview:hover {
  cursor: zoom-in
}

.arco-image-error {
  width: 100%;
  height: 100%;
  background-color: var(--color-neutral-1);
  color: var(--color-neutral-4);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center
}

.arco-image-error-icon {
  width: 60px;
  height: 60px;
  max-width: 100%;
  max-height: 100%
}

.arco-image-error-icon>svg {
  width: 100%;
  height: 100%
}

.arco-image-error-alt {
  font-size: 12px;
  line-height: 1.6667;
  text-align: center;
  padding: 8px 16px
}

.arco-image-loader {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--color-neutral-1)
}

.arco-image-loader-spin {
  font-size: 32px;
  transform: translate(-50%,-50%);
  position: absolute;
  color: rgb(var(--primary-6));
  left: 50%;
  top: 50%;
  text-align: center
}

.arco-image-loader-spin-text {
  color: var(--color-neutral-6);
  font-size: 16px
}

.arco-image-simple.arco-image-with-footer-inner .arco-image-footer {
  padding: 12px 16px
}

.arco-image-loading .arco-image-img,.arco-image-loading-error .arco-image-img {
  visibility: hidden
}

.arco-image-trigger .arco-image-actions-list {
  flex-direction: column
}

.arco-image-trigger .arco-image-actions-item {
  color: var(--color-neutral-8);
  margin-left: 0
}

.arco-image-trigger .arco-image-actions-item:hover {
  background: var(--color-neutral-2)
}

.arco-image-preview {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1001
}

.arco-image-preview-hide {
  display: none
}

.arco-image-preview-mask,.arco-image-preview-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0
}

.arco-image-preview-mask {
  background-color: var(--color-mask-bg)
}

.arco-image-preview-img-container {
  width: 100%;
  height: 100%;
  text-align: center
}

.arco-image-preview-img-container:before {
  content: "";
  width: 0;
  height: 100%;
  vertical-align: middle;
  display: inline-block
}

.arco-image-preview-img-container .arco-image-preview-img {
  max-width: 100%;
  max-height: 100%;
  display: inline-block;
  vertical-align: middle;
  user-select: none;
  cursor: grab
}

.arco-image-preview-img-container .arco-image-preview-img.arco-image-preview-img-moving {
  cursor: grabbing
}

.arco-image-preview-scale-value {
  padding: 7px 10px;
  box-sizing: border-box;
  font-size: 12px;
  color: var(--color-white);
  background-color: #ffffff14;
  line-height: initial;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%)
}

.arco-image-preview-toolbar {
  background-color: var(--color-bg-2);
  border-radius: var(--border-radius-medium);
  display: flex;
  align-items: flex-start;
  padding: 4px 16px;
  position: absolute;
  bottom: 46px;
  left: 50%;
  transform: translate(-50%)
}

.arco-image-preview-toolbar-action {
  font-size: 14px;
  color: var(--color-neutral-8);
  border-radius: var(--border-radius-small);
  background-color: transparent;
  cursor: pointer;
  display: flex;
  align-items: center
}

.arco-image-preview-toolbar-action:not(:last-of-type) {
  margin-right: 0
}

.arco-image-preview-toolbar-action:hover {
  background-color: var(--color-neutral-2);
  color: rgb(var(--primary-6))
}

.arco-image-preview-toolbar-action-disabled,.arco-image-preview-toolbar-action-disabled:hover {
  color: var(--color-text-4);
  background-color: transparent;
  cursor: not-allowed
}

.arco-image-preview-toolbar-action-name {
  font-size: 12px;
  padding-right: 12px
}

.arco-image-preview-toolbar-action-content {
  padding: 13px;
  line-height: 1
}

.arco-image-preview-toolbar-simple {
  padding: 4px
}

.arco-image-preview-toolbar-simple .arco-image-preview-toolbar-action {
  margin-right: 0
}

.arco-image-preview-trigger.arco-image-trigger {
