html,body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 14px;
  background-color: var(--color-bg-1)
}

html ::-webkit-scrollbar,body ::-webkit-scrollbar {
  width: 8px;
  height: 8px
}

html ::-webkit-scrollbar-track,body ::-webkit-scrollbar-track {
  border-radius: 6px;
  background: rgba(0,0,0,.06);
  -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,.08)
}

html ::-webkit-scrollbar-thumb,body ::-webkit-scrollbar-thumb {
  border-radius: 6px;
  background: rgba(0,0,0,.12);
  -webkit-box-shadow: inset 0 0 10px rgba(0,0,0,.2)
}

.chart-wrapper .bizcharts-tooltip {
  background: linear-gradient(304.17deg,rgba(253,254,255,.6) -6.04%,rgba(244,247,252,.6) 85.2%)!important;
  border-radius: 6px;
  backdrop-filter: blur(10px);
  padding: 8px!important;
  width: 180px!important;
  opacity: 1!important
}

body[arco-theme=dark] .chart-wrapper .bizcharts-tooltip {
  background: linear-gradient(304.17deg,rgba(90,92,95,.6) -6.04%,rgba(87,87,87,.6) 85.2%)!important;
  backdrop-filter: blur(10px);
  border-radius: 6px;
  box-shadow: none!important
}

.slate-HeadingToolbar {
  padding: 17px 60px!important
}

.tippy-box[data-theme~=tomato] {
  background-color: tomato;
  color: #ff0
}

.drag-button {
  overflow: hidden;
  padding: 0;
  background-color: transparent;
  background-repeat: no-repeat;
  border-style: none;
  cursor: pointer;
  outline: 0;
  min-width: 18px;
  min-height: 18px
}

.MuiSvgIcon-root {
  font-size: 18px!important
}

.arco-trigger {
  position: absolute;
  z-index: 1000;
  backface-visibility: hidden
}

.arco-trigger-arrow {
  background-color: var(--color-bg-5);
  content: "";
  height: 8px;
  width: 8px;
  position: absolute;
  display: block;
  box-sizing: border-box;
  transform: rotate(45deg);
  transform-origin: 50% 50% 0;
  z-index: -1
}

.arco-trigger[trigger-placement=top] .arco-trigger-arrow,.arco-trigger[trigger-placement=tl] .arco-trigger-arrow,.arco-trigger[trigger-placement=tr] .arco-trigger-arrow {
  bottom: -4px;
  margin-left: -4px;
  border-top: none;
  border-left: none;
  border-bottom-right-radius: 2px
}

.arco-trigger[trigger-placement=bottom] .arco-trigger-arrow,.arco-trigger[trigger-placement=bl] .arco-trigger-arrow,.arco-trigger[trigger-placement=br] .arco-trigger-arrow {
  top: -4px;
  margin-left: -4px;
  border-bottom: none;
  border-right: none;
  border-top-left-radius: 2px
}

.arco-trigger[trigger-placement=left] .arco-trigger-arrow,.arco-trigger[trigger-placement=lt] .arco-trigger-arrow,.arco-trigger[trigger-placement=lb] .arco-trigger-arrow {
  right: -4px;
  margin-top: -4px;
  border-left: none;
  border-bottom: none;
  border-top-right-radius: 2px
}

.arco-trigger[trigger-placement=right] .arco-trigger-arrow,.arco-trigger[trigger-placement=rt] .arco-trigger-arrow,.arco-trigger[trigger-placement=rb] .arco-trigger-arrow {
  left: -4px;
  margin-top: -4px;
  border-top: none;
  border-right: none;
  border-bottom-left-radius: 2px
}

.arco-empty {
  width: 100%;
  padding: 10px 0;
  box-sizing: border-box
}

.arco-empty .arco-empty-wrapper {
  width: 100%;
  box-sizing: border-box;
  text-align: center;
  color: rgb(var(--gray-5))
}

.arco-empty .arco-empty-wrapper .arco-empty-image {
  font-size: 48px;
  line-height: 1;
  margin-bottom: 4px
}

.arco-empty .arco-empty-wrapper img {
  height: 80px
}

.arco-empty .arco-empty-description {
  color: rgb(var(--gray-5));
  font-size: 14px
}

.arco-icon-hover.arco-checkbox-icon-hover:before {
  width: 24px;
  height: 24px
}

.arco-checkbox {
  display: inline-block;
  cursor: pointer;
  box-sizing: border-box;
  font-size: 14px;
  padding-left: 5px;
  line-height: unset;
  position: relative
}

.arco-checkbox>input[type=checkbox] {
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
  width: 0;
  height: 0
}

.arco-checkbox>input[type=checkbox]:focus-visible+.arco-checkbox-icon-hover:before {
  background-color: var(--color-fill-2);
  opacity: 1
}

.arco-checkbox:hover .arco-checkbox-icon-hover:before {
  background-color: var(--color-fill-2)
}

.arco-checkbox-text {
  color: var(--color-text-1);
  margin-left: 8px
}

.arco-checkbox-mask-wrapper {
  vertical-align: middle;
  top: -.09em;
  position: relative;
  line-height: 1
}

.arco-checkbox-mask {
  position: relative;
  box-sizing: border-box;
  width: 14px;
  height: 14px;
  border: 2px solid var(--color-fill-3);
  border-radius: var(--border-radius-small);
  background-color: var(--color-bg-2);
  user-select: none
}

.arco-checkbox-mask:after {
  content: "";
  display: block;
  height: 2px;
  width: 6px;
  background: var(--color-white);
  top: 50%;
  left: 50%;
  transform: translate(-50%) translateY(-50%) scale(0);
  position: absolute;
  border-radius: .5px
}

.arco-checkbox-mask-icon {
  position: relative;
  height: 100%;
  transform: scale(0);
  color: var(--color-white);
  transform-origin: center 75%;
  margin: 0 auto;
  display: block;
  width: 8px
}

.arco-checkbox:hover .arco-checkbox-mask {
  border-color: var(--color-fill-4);
  transition: border-color .1s cubic-bezier(0,0,1,1),transform .3s cubic-bezier(.3,1.3,.3,1)
}

.arco-checkbox-checked:hover .arco-checkbox-mask,.arco-checkbox-indeterminate:hover .arco-checkbox-mask {
  transition: transform .3s cubic-bezier(.3,1.3,.3,1)
}

.arco-checkbox-checked .arco-checkbox-mask {
  border-color: transparent;
  background-color: rgb(var(--primary-6))
}

.arco-checkbox-checked .arco-checkbox-mask-icon {
  transform: scale(1);
  transition: transform .3s cubic-bezier(.3,1.3,.3,1)
}

.arco-checkbox-indeterminate .arco-checkbox-mask {
  border-color: transparent;
  background-color: rgb(var(--primary-6))
}

.arco-checkbox-indeterminate .arco-checkbox-mask-icon {
  transform: scale(0)
}

.arco-checkbox-indeterminate .arco-checkbox-mask:after {
  transform: translate(-50%) translateY(-50%) scale(1);
  transition: transform .3s cubic-bezier(.3,1.3,.3,1)
}

.arco-checkbox.arco-checkbox-disabled,.arco-checkbox.arco-checkbox-disabled .arco-checkbox-icon-hover {
  cursor: not-allowed
}

.arco-checkbox.arco-checkbox-disabled:hover .arco-checkbox-mask {
  border-color: var(--color-fill-3)
}

.arco-checkbox-checked:hover .arco-checkbox-mask,.arco-checkbox-indeterminate:hover .arco-checkbox-mask {
  border-color: transparent
}

.arco-checkbox-disabled .arco-checkbox-mask {
  border-color: var(--color-fill-3);
  background-color: var(--color-fill-2)
}

.arco-checkbox-disabled.arco-checkbox-checked .arco-checkbox-mask,.arco-checkbox-disabled.arco-checkbox-checked:hover .arco-checkbox-mask,.arco-checkbox-disabled.arco-checkbox-indeterminate .arco-checkbox-mask,.arco-checkbox-disabled.arco-checkbox-indeterminate:hover .arco-checkbox-mask {
  border-color: transparent;
  background-color: var(--color-primary-light-3)
}

.arco-checkbox-disabled:hover .arco-checkbox-mask-wrapper:before,.arco-checkbox-checked:hover .arco-checkbox-mask-wrapper:before,.arco-checkbox-indeterminate:hover .arco-checkbox-mask-wrapper:before {
  background-color: transparent
}

.arco-checkbox-disabled .arco-checkbox-text {
  color: var(--color-text-4)
}

.arco-checkbox-disabled .arco-checkbox-mask-icon {
  color: var(--color-fill-3)
}

.arco-checkbox-group {
  display: inline-block
}

.arco-checkbox-group .arco-checkbox {
  margin-right: 16px
}

.arco-checkbox-group-direction-vertical .arco-checkbox {
  display: block;
  margin-right: 0;
  line-height: 32px
}

.arco-checkbox-rtl {
  direction: rtl;
