/* Reset any margins and paddings to make sure we start with a clean slate */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Body styling for a consistent font */
  body {
    font-family: 'Arial', sans-serif; /* This should be your brand's font if different */
    line-height: 1.6;
    color: #333;
    background-color: #f4f4f4; /* Slight grey background to reduce eye strain */
  }
  
  /* Container to center everything and give some padding */
  .container {
    max-width: 1200px;
    margin: auto;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    gap: 2rem; /* Creates consistent space between child elements */
  }
  
  /* Header styling */
  .header {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 2rem;
  }
  
  /* Card styling */
  .card {
    border: 1px solid var(--color-border-2);
    border-radius: 8px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    padding: 1.5rem; /* More padding for a better look */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: box-shadow 0.3s ease;
    cursor: pointer;
  }
  
  .card:hover {
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  }
  
  /* Improved checkbox card styling */
  .custom-checkbox-card {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 10px; /* Space between checkbox and title */
    padding: 20px;
    width: 100%; /* Take the full width of the parent */
    background-color: var(--color-white);
    border: 1px solid var(--color-border-2);
    border-radius: 8px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
    transition: all 0.3s;
  }
  
  /* Styling for when checkbox is focused or checked */
  input[type='checkbox']:focus-visible + .custom-checkbox-card,
  .custom-checkbox-card-checked {
    box-shadow: 0 0 0 2px var(--color-primary-light-3);
    background-color: var(--color-primary-light-1);
  }
  
  /* Title and description within checkbox card */
  .custom-checkbox-card-title {
    font-size: 16px;
    font-weight: bold;
  }
  
  .custom-checkbox-card-description {
    color: #666;
    font-size: 14px;
    margin-top: 5px; /* Space between title and description */
  }
  
  /* Button Styling */
  .btn-primary {
    background-color: #007bff;
    border-color: #007bff;
    padding: 10px 20px;
    font-size: 16px; /* Larger font size for better readability */
    font-weight: 500;
    text-align: center;
    color: #fff;
    border-radius: 8px;
    cursor: pointer;
    outline: none;
    transition: background-color 0.3s ease;
  }
  
  .btn-primary:hover {
    background-color: #0056b3;
    border-color: #0056b3;
  }
  
  /* Layout of cards in a grid with even spacing */
  .row {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 1rem;
  }
  
  /* Footer styling */
  .arco-layout-footer {
    text-align: center;
    padding: 1rem 0;
    font-size: 14px;
    color: #666;
  }
  
  /* Responsive styling */
  @media (max-width: 768px) {
    .btn-primary {
      padding: 8px 16px;
      font-size: 14px;
    }
  }

